import styled from "styled-components";
import { StarOutlined } from "../icons";

export const StarButton = styled(StarOutlined)<{ $active?: boolean }>((p) => ({
  width: 16,
  height: 16,
  cursor: "pointer",
  path: p.$active
    ? {
        stroke: p.theme.colors.star,
        fill: p.theme.colors.star,
      }
    : {},
}));
